.navigation-bar-box {
    display: flex;
    justify-content: center;
    margin: 5px;
    padding: 5px;
    background-color: hsla(0,0%,100%,.4);
}

.navigation-bar {
    text-align: center;
    display: flex;
    flex-direction: row;
    width: 1000px;
    font-size: 12px;
}

.navigation-left-box {
    width: 600px;
    display: flex;
    flex-direction: row;
}

.navigation-right-box {
    width: 400px;
    display: flex;
    flex-direction: row;
}

.navigation-item {
    width: 150px;
    font-size: 12px;
}

.navigation-myface {
    height: 42px;
    border-radius: 50%;
}

.navigation-item {
    float: left;
    position: relative;
    text-align: center;
    line-height: 42px;
    height: 100%;
    background-color: hsla(0,0%,100%,0);
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    list-style: none;
    font-family: "Helvetica Neue", Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "Heiti SC",
    "WenQuanYi Micro Hei", sans-serif;
}
